@import url(https://fonts.googleapis.com/css2?family=Exo:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
.received{padding:30px 80px}.received .received-list{width:100%;display:flex;justify-content:space-around;font-family:"Exo", sans-serif}.received .received-list div{width:120px}


.items-list{font-family:"Exo", sans-serif}.items-list .ant-table-tbody>tr>td{font-size:15px;padding:25px}.items-list button{border:none;background-color:#40fdd72d;cursor:pointer;transition:background-color 0.5s ease;margin-left:40px}.items-list button:hover{background-color:#40fdd788}.items-list .total{float:right;padding:10px}@media only screen and (max-width: 600px){.items-list .ant-table-tbody>tr>td{font-size:8px;padding:5px}.items-list button{margin-left:unset}.total{font-size:10px}}

.board{padding:0 20px}.board-card{min-height:550px;border:none}.board .ant-card-body{display:flex;justify-content:space-between}.board-list{width:800px}.board-form{width:300px;margin-left:20px}@media only screen and (max-width: 600px){.ant-card-body{display:flex;flex-direction:column}.board-list{width:unset}.board-form{width:unset;margin:unset;margin-top:10px}}

.store{font-family:"Exo", sans-serif;font-size:15px}.store .ant-card-body{min-height:550px;display:flex}.store-list{min-height:200px;max-height:300px;min-width:200px;max-width:300px;padding:20px;margin:20px;border-style:outset;box-shadow:0 10px 6px -6px #a7a6a6}.store-list-title{text-decoration:underline}.store-list-item{display:flex;justify-content:space-between;margin-top:10px}.ant-tabs{padding:0 20px}@media only screen and (max-width: 600px){.store .ant-tabs{padding:20px;width:unset}}

.home .ant-tabs-nav{margin:0}.home .ant-tabs-nav-wrap{margin-left:20px}

.navbar{display:flex;background:#2e2f30;color:#40fdd7;box-shadow:0 4px 2px -2px #00000033;font-family:"Oswald", sans-serif;justify-content:space-between}.navbar-title{padding:12px 20px;font-size:35px}.navbar-links{display:flex;font-size:25px;padding:20px}.navbar-links .link{color:#40fdd7;margin-right:15px;transition:color 0.5s ease}.navbar-links .link:hover{color:#cae4df}@media only screen and (max-width: 600px){.navbar-title{margin-left:8px}.navbar-links{font-size:20px;padding:25px 15px}}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f9fafb;
}


