@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap");

.navbar {
  display: flex;
  background: #2e2f30;
  color: #40fdd7;
  box-shadow: 0 4px 2px -2px #00000033;
  font-family: "Oswald", sans-serif;
  justify-content: space-between;

  &-title {
    padding: 12px 20px;
    font-size: 35px;
  }

  &-links {
    display: flex;
    font-size: 25px;
    padding: 20px;

    .link {
      color: #40fdd7;
      margin-right: 15px;
      transition: color 0.5s ease;
    }
    .link:hover {
      color: #cae4df;
    }
  }
}

@media only screen and (max-width: 600px) {
  .navbar-title {
    margin-left: 8px;
  }
  .navbar-links {
    font-size: 20px;
    padding: 25px 15px;
  }
}
